// uniq id generator
// https://stackoverflow.com/questions/105034/how-to-create-a-guid-uuid
function randomId() {
  //let template = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx';
  let template = 'xxxxxxxxxx'
  return template.replace(/[xy]/g, function(c) {
    var r = Math.random()*36|0, v = c == 'x' ? r : (r&0x3|0x8);
    let char = v.toString(36);
    return Math.random() > 0.5 ? char : char.toUpperCase();
  });
}

function meanAndSd(arr) {
  let n = arr.length
  let mean = arr.reduce((a, c) => a + c, 0) / n
  let sd = Math.sqrt(arr.map(x => Math.pow(x - mean, 2)).reduce((a, c) => a + c) / (n-1), 0)
  return [mean, sd]
}

const getPrecision = (numDecimal, nSD) => {
  //console.log(numDecimal.abs().toSD(2).toExponential())
  let exp = parseFloat(numDecimal.abs().toSD(2).toExponential().split('e')[1])
  let precision = (-exp)+nSD-1
  return precision
}

const formatToPrecision = (numDecimal, precision) => {
  precision = Math.max(0, precision)
  // quick fix
  if (isNaN(precision)) {
    precision = 0
  }
  let numStr = numDecimal.toFixed(precision).toString()
  
  // fix -0.0 bug
  if (parseFloat(numStr) === 0 && (numStr[0] === '-')) {
    return numStr.slice(1)
  }
  return numStr
}

function linspace(startValue, stopValue, cardinality) {
  var arr = [];
  var step = (stopValue - startValue) / (cardinality - 1);
  for (var i = 0; i < cardinality; i++) {
    arr.push(startValue + (step * i));
  }
  return arr;
}

export {randomId, meanAndSd, getPrecision, formatToPrecision, linspace}