import {meanAndSd, getPrecision, formatToPrecision} from './utils.js'
import { Decimal } from 'decimal.js'
Decimal.set({ precision: 13, rounding: 6 })
window.Decimal = Decimal


const exprOperatorsRegex = new RegExp(/[\s+*\-()%^=&|/:]+/)
const startWithDigitOrReservedRegex = new RegExp(/^[\d_]/)


export default {
  methods: {
    getExprVars (expr) {
      return expr.split(exprOperatorsRegex)
        // remove empty
        .filter(item => item.trim().length)
        // start with digit
        .filter(item => !item.match(startWithDigitOrReservedRegex))
        .filter(item => !this.exprTokens.includes(item))
        .map(item => item.trim())
    }
  },  
  computed: {
    prefixesTuples () {
      return [
        ["Y", 1e24],
        ["Z", 1e21],
        ["E", 1e18],
        ["P", 1e15],
        ["T", 1e12],
        ["G", 1e9],
        ["M", 1e6],
        ["k", 1e3],
        ["h", 1e2],
        ["da", 1e1],
        ["", 1],
        ["d", 1e-1],
        ["c", 1e-2],
        ["m", 1e-3],
        ["µ", 1e-6],
        ["n", 1e-9],
        ["p", 1e-12],
        ["f", 1e-15],
        ["a", 1e-18],
        ["z", 1e-21],
        ["y", 1e-24],
      ]
    },
    prfx () {
      let res = this.prefixesTuples.reduce((a, c) => {
        a[c[0]] = c[1]
        return a
      }, {})
      res[undefined] = 1
      return res
    },
    currentWbObj () {
      return this.notebook.wbs.filter(wb => wb.id === this.notebook.currentWb)[0]
    },
    visibleVars () {
      return Object.keys(this.cellsVarValuesTuples)
        .map(k => this.cellsVarValuesTuples[k]
          .filter(tuple => tuple[2] === this.notebook.currentWb) // show got selected notebook
          .map(tuple => tuple[0])
        )
        .reduce((a, c) => a.concat(c), [])
        .reduce((a, c) => a.indexOf(c) === -1 ? !a.push(c)||a : a, [])
    },
    // cellsDates () {
    //   return this.notebook.cells.map(cell => cell.time.split('T')[0])
    //   .reduce((a, c) => a.indexOf(c) === -1 ? !a.push(c)||a : a, [])
    //   .sort((a, b) => a.localeCompare(b))
    // },
    localWbAsAssets () {
      return this.notebook.wbs.map(wb => {
        let wbAsAsset = JSON.parse(JSON.stringify(wb))
        wbAsAsset.cells = this.notebook.cells.filter(cell => cell.wb === wb.id)
        return wbAsAsset
      })
    },
    lerpVars () {
      return this.localWbAsAssets.concat(this.currentWbObj.assets).reduce((a, c) => {
        a[c.id] = a[c.id] || []
        
        a[c.id] = a[c.id].concat(
          c.cells.map(cell =>
            cell.data.map(q => q.var) // get vars name
            .filter(item => this.getExprVars(cell.expr).includes(item)) // remove vars not in expr
          ).flat().reduce((a, c) => a.indexOf(c) ? !a.push(c)||a : a,[]) // get uniq
        )
        return a
      }, {})
    },
    assetsCells () {
      return this.currentWbObj.assets.map(asset => asset.cells).flat()
    },
    cellsVarValuesObjects () {
      return this.notebook.cells.concat(this.assetsCells).reduce((a, cell) => {
        let obj = {}
        
        if (!this.cellsVarValuesTuples[cell.id]) return a

        this.cellsVarValuesTuples[cell.id].forEach(item => {
          obj[item[0]] = item[1]
        })

        a[cell.id] = obj
        return a
      }, {})
    },
    cellsVarValuesTuples () {
      return this.notebook.cells.concat(this.assetsCells).reduce((a, cell, i) => {
        if (!cell.data) return a

        let wb = cell.wb

        let varValues = cell.data.map(item => {
          // cell data can have a cached version of a var deleted from expression.
          // This is a feature because it's useful to avoid user from loosing data
          // defined on parameter field. Here we check and skeep a var on cache
          if (this.expressionsVars[i].indexOf(item.var) === -1) return

          if (["Normal", "StudentsT"].indexOf(item.dist) !== -1) return [item.var, item.args[0], wb]

          if (item.dist === "Input") {
            let floatArray = item.args.map(parseFloat).filter(x => !isNaN(x))

            if (!floatArray.length) return [item.var, NaN, wb]

            let [mean, sd] = meanAndSd(floatArray.map(x => parseFloat(x)*this.prfx[item.pref]))

            let u = sd / Math.sqrt(floatArray.length)

            let meanDec = new Decimal(mean)
            let uDec = new Decimal(u)

            let nSD = 4
            
            let quantity = cell.data.filter(d => d.var === item.var)[0]
            
            quantity.y = formatToPrecision(meanDec, getPrecision(uDec, nSD))
            quantity.u = formatToPrecision(uDec, getPrecision(uDec, nSD))

            return [item.var, mean*this.prfx[item.pref], wb]
          }

          if (item.dist === "Resol") {
            return [item.var, 0, wb]
          }

          //filter interpolation have `object` type
          if (typeof item.dist === 'object') {
            return [item.var, (item.y ? item.y : undefined), wb]
          }

          //["Uniform","Arcsine","Trap","CTrap", "Triangular"]
          return [item.var, (item.args[0] + item.args[1]) / 2, wb]
          // TODO: warn about the use of asymetric Triangular distribution
        })
        // filter skeeped vars
        .filter(item => item !== undefined)

        a[cell.id] = varValues
        return a
      }, {})

    },
    expressionsVars () {
      return this.notebook.cells.concat(this.assetsCells).map(cell => {
        return this.getExprVars(cell.expr)
      })
    },
    exprTokens () {
      return 'log base e pi int ceil floor round modulus abs sign min max sin cos tan sinh cosh tanh asin acos atan asinh acosh atanh'.split(' ')
    },
    distributions () {
      return this.distributionsBasic.concat(this.distributionsCommon)
    },
    distributionsCommon () {
      return [
        'Input',
        'Resol'
      ]
    },
    distributionsBasic () {
      return [
        'Uniform',
        'Normal',
        'StudentsT',
        'Triangular',
        //'LogNormal',
        'Arcsine',
        'CTrap',
        'Trap'

      ]
    },
    distributionsCommonLabel () {
      return Object({
        'Input': 'Input data',
        'Resol': 'Resolution'
      })
    },
    units () {
      return this.notebook.cells.filter(cell => cell.unit !== undefined).map(cell => cell.unit.trim())
    },
    unitsUniq () {
      return this.units.reduce((a, c) => a.indexOf(c) === -1 ? !a.push(c)||a : a, [])
        .sort((a, b) => a.localeCompare(b))
    },
    exportedPiecewise () {
      return this.notebook.cells.map(cell => {
        
        // has cell a uniq tag?
        if (!cell.tags) return
        let uniqTagsByCell = cell.tags.reduce((a, c) => this.uniqTagsByCell.indexOf(c) !== -1 ? !a.push(c)||a : a, [])
        if (!uniqTagsByCell.length) return

        return {desc: uniqTagsByCell.toString(), id: cell.id}
      })
      // get non undefined
      .filter(item => item)
    },
    currentWbAssetsToInterpoale () {
      return this.notebook.wbs.concat(this.currentWbObj.assets
        .map(asset => Object({id: asset.id, name: `${asset.desc}➙${asset.name}`}))
      )
    }
  }
}